"use client";
import React, { useEffect, useState } from "react";
import Logo from "./Logo";
import "./Header.css";

const NAV_LINKS = [
  { href: "#services", label: "Services" },
  { href: "#contact", label: "Contact" },
];

// Utility function to validate hrefs (prevents javascript: and other XSS vectors)
const isSafeHref = (href) => /^#|^\/|^https?:\/\//.test(href);

const Header = () => {
  const [isShrunk, setIsShrunk] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsShrunk(window.scrollY > 30);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header className={`header${isShrunk ? " shrunk" : ""}`}>
      <div className="header-inner">
        <div className="header-left">
          <a className="logo-link" href="/">
            <Logo />
          </a>
        </div>
        <nav className="main-nav">
          <ul>
            {NAV_LINKS.map((link) => (
              <li key={link.href}>
                <a href={isSafeHref(link.href) ? link.href : "#"}>{link.label}</a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </header>
  );
};

export default Header;